/*
 *
 * dawn
 *
 */
@import 'shared';

// mixin to extend dawn filter
// @mixin dawn
// @param $filters... {filter} - Zero to many css filters to be added
// @example
//   img {
//     @include dawn;
//   }
//   or
//   img {
//     @include dawn(blur(2px));
//   }
//   or
//   img {
//     @include dawn(blur(2px)) {
//       /*...*/
//     };
//   }
@mixin dawn($filters...) {
  @include filter-base;
  filter: none $filters;

  img {
    filter:grayscale(1);
  }

  &::after {
  /*  background: linear-gradient(to bottom, darken(#19547B, 22%), darken(#9A69B0, 0%)); */
    mix-blend-mode: overlay;
  }

  @content;
}

// dawn Instagram filter
%dawn,
.dawn {
  @include dawn;
}
